<p>
&emsp;We mean the literal kind—the one that involves hours of staring at a paper and figuring out the word or the number in a crossword or Sudoku puzzle. “Whatever your age, ongoing mental stimulation will keep your brain [from] building new spines on the dendrites of your brain cells,” says Dr. Thomas Crook, author of The Memory Cure. Taking up a new hobby and learning a new skill such as playing the piano can also do wonders for your brain.
</p>
